import React from 'react';
import { Panel, Avatar, PanelHeader, List, Cell, Spinner } from '@vkontakte/vkui';
import Counter from '@vkontakte/vkui/dist/components/Counter/Counter';

const Top = ({ id, fetchedUser, top, spinner }) => (
	<Panel id={id}>
		<PanelHeader>Топ пользователей</PanelHeader>

        {spinner &&
		<div style={{display: 'flex', alignItems: 'center', flexDirection: 'colomn'}}>
			<Spinner size="large" style={{ marginTop: 20 }}/>
        </div>}
        
        {top.length > 0 && top.map((item, index) => (
            <List>
                <Cell
                data-top-id={item.id}
                indicator={item.id}
                href={'//vk.com/id' + item.user_id}
                key={index}
                title="Король лайков"
                description={item.p_likes + item.p_flikes + item.p_tlikes + item.p_alllikes + item.p_comm + item.p_gposts + item.p_gcomm}
                before={item.photo ? <Avatar src={item.photo}/> : <Avatar src='https://vk.com/images/camera_100.png'/>}
                >{item.username ? item.username : 'Unknown'}</Cell>
            </List>
        ))

        }
	</Panel>
);

export default Top;